import { ORDER_CREATE_FAIL, ORDER_CREATE_REQUEST, ORDER_CREATE_RESET, ORDER_CREATE_SUCCESS,
  ORDER_DELETE_FAIL,
  ORDER_DELETE_REQUEST,
  ORDER_DELETE_RESET,
  ORDER_DELETE_SUCCESS,
  ORDER_DELIVER_FAIL,
  ORDER_DELIVER_REQUEST,
  ORDER_DELIVER_RESET,
  ORDER_DELIVER_SUCCESS,
   ORDER_DETAILS_FAIL, ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS, ORDER_LIST_FAIL, ORDER_LIST_REQUEST, ORDER_LIST_SUCCESS, ORDER_PAY_FAIL,
    ORDER_PAY_REQUEST, ORDER_PAY_RESET, ORDER_PAY_SUCCESS, ORDER_PERSONAL_FAIL, ORDER_PERSONAL_REQUEST, ORDER_PERSONAL_SUCCESS, RESERVATION_CREATE_FAIL, RESERVATION_CREATE_REQUEST, RESERVATION_CREATE_SUCCESS, RESERVATION_DELETE_FAIL, RESERVATION_DELETE_REQUEST, RESERVATION_DELETE_RESET, RESERVATION_DELETE_SUCCESS, RESERVATION_LIST_FAIL, RESERVATION_LIST_REQUEST, RESERVATION_LIST_SUCCESS } from "../redux-constants/orderConstants";


export const orderCreateReducer = (state = {}, action) => {
    switch (action.type) {
      case ORDER_CREATE_REQUEST:
        return { loading: true };
      case ORDER_CREATE_SUCCESS:
        return { loading: false, success: true, order: action.payload };
      case ORDER_CREATE_FAIL:
        return { loading: false, error: action.payload };
      case ORDER_CREATE_RESET:
        return {};
      default:
        return state;
    }
  };
export const reservationCreateReducer = (state = {}, action) => {
    switch (action.type) {
      case RESERVATION_CREATE_REQUEST:
        return { loading: true };
      case RESERVATION_CREATE_SUCCESS:
        return { loading: false, success: true, reservation: action.payload };
      case RESERVATION_CREATE_FAIL:
        return { loading: false, error: action.payload };
      default:
        return state;
    }
  };
